.q-qsc__subnav-link {
	text-decoration: none;
	color: #262626;
	font-size: 15px;
	letter-spacing: -0.2px;
	transition: color 0.3s;
	position: relative;
}
.subnav-active:after {
	content: "";
	height: 4px;
	width: 58em;
	background: #B5CFE5;
	position: absolute;
	bottom: -21px;
	right: -10px;
}
.subnav-city {
	position: relative;
}
.subnav-city:after {
	content: "";
	height: 4px;
	width: 18em;
	/*background: #B5CFE5;*/
	position: absolute;
	bottom: -21px;
	right: -10px;
}
.q-qsc__subnav-link:hover {
	color: #717171;
}
.q-qsc__subnav-link--register {
	display: flex;
	align-items: center;
}
.user-icon {
	position: relative;
}
@keyframes spin {
	from {
		transform: rotateZ(0deg);
	}
	to {
		transform: rotateZ(180deg);
	}
}
.plus-icon {
	position: absolute;
	width: 10px;
	height: 10px;
	top: 9px;
	left: 37px;
	display: inline-block;
}
.q-qsc__subnav-link:hover .plus-icon {
	animation: spin 0.5s 1;
}
.plus-icon::before {
	content: "";
	background: #FFF;
	width: 10px;
	height: 2px;
	top: 4px;
	position: absolute;
	left: 0;
}
.plus-icon::after {
	content: "";
	background: #FFF;
	width: 2px;
	height: 10px;
	top: 0;
	position: absolute;
	left: 4px;
}
