@media (min-width: 720px) {
	.conference-title-wrapper.label:before,
	.conference-title-wrapper.label:after {
		content: "";
		position: absolute;
		background: #B5D0E5;
	}
	.conference-title-wrapper.label:before {
		transition: all 0.35s ease-in-out;
		width: 20px;
		height: 2px;
		left: 0.8em;
		top: 30px;
	}
	.conference-title-wrapper.label:after {
		transition: all 0.35s ease-in-out;
		transform: rotate(-90deg);
		opacity: 1;
		left: 0.8em;
		top: 30px;
		width: 20px;
		height: 2px;
	}
}
.opened {
